<template>
  <div>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="id" label="">
      </el-table-column>
      <el-table-column prop="college" label="">
      </el-table-column>
      <el-table-column prop="type" label=""
                       :filters=this.typeList
                       :filter-method="filterType">
        <template slot-scope="scope">
          <el-tag>{{scope.row.type}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="temail" label="">
      </el-table-column>
      <el-table-column prop="size" label="">
      </el-table-column>
      <el-table-column prop="period" label="">
      </el-table-column>
      <el-table-column prop="price" label="">
      </el-table-column>
      <el-table-column label="" width="200">
        <template slot-scope="scope">
          <el-popover placement="right" width="400" trigger="click">
            <el-table :data="scope.row.scheduleList">
              <el-table-column property="courseid" label=""></el-table-column>
              <el-table-column property="starttime" label=""></el-table-column>
              <el-table-column property="endtime" label=""></el-table-column>
            </el-table>
            <el-button size="mini" type="primary" slot="reference"></el-button>
          </el-popover>
          <el-popover placement="right" width="400" trigger="click">
            <el-table :data="scope.row.studentList">
              <el-table-column property="email" label=""></el-table-column>
              <el-table-column property="name" label=""></el-table-column>
            </el-table>
            <el-button size="mini" type="primary" slot="reference"></el-button>
          </el-popover>
          <el-button v-if="userType === 'college'" size="mini" type="warning"
                     @click="handleEdit(scope.$index, scope.row)">
          </el-button>
          <el-button v-if="userType === 'college'" size="mini" type="warning"
                     @click="handleAdd(scope.$index, scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-form :model="form" :rules="rules" ref="form" label-width="80px" v-show="form.seen">
      <el-form-item label="" prop="id">
        <el-input :disabled=true size="medium" v-model="form.id"></el-input>
      </el-form-item>
      <el-form-item label="" prop="collegeId">
        <el-input :disabled=true size="medium" v-model="form.collegeId"></el-input>
      </el-form-item>
      <el-form-item label="" prop="type">
        <el-select v-model="form.type" placeholder="">
          <el-option label="" value="math"></el-option>
          <el-option label="" value="language"></el-option>
          <el-option label="" value="coding"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="temail">
        <el-input size="medium" v-model="form.temail"></el-input>
      </el-form-item>
      <el-form-item label="" prop="size">
        <el-input size="medium" v-model="form.size"></el-input>
      </el-form-item>
      <el-form-item label="" prop="period">
        <el-input size="medium" v-model="form.period"></el-input>
      </el-form-item>
      <el-form-item label="" prop="price">
        <el-input size="medium" v-model="form.price"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-row v-for="(row ,index) in form.scheduleList">
          <el-date-picker type="datetime" placeholder="" v-model="row.starttime"></el-date-picker>
          <span>-</span>
          <el-date-picker type="datetime" placeholder="" v-model="row.endtime"></el-date-picker>
          <el-button type="danger" @click="removeSchedule(row, index)"></el-button>
        </el-row>
        <el-button type="primary" @click="addSchedule"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submit('form')"></el-button>
        <el-button @click="reset('form')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: "courses",
    data() {
      return {
        tableData: [],
        typeList: [],
        userType: window.sessionStorage.getItem('userType'),
        form: {
          seen: false,
          addOperation: false,
          id: '',
          collegeId: '',
          type: '',
          temail: '',
          size: '',
          period: '',
          price: '',
          scheduleList: [],
          studentList: []
        },
        rules: {
          type: [
            {required: true, message: '', trigger: 'change'}
          ],
          temail: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'email', message: '', trigger: 'blur,change'}
          ],
          size: [
            {required: true, message: '', trigger: 'blur'}
          ],
          period: [
            {required: true, message: '', trigger: 'blur'}
          ],
          price: [
            {required: true, message: '', trigger: 'blur'}
          ]
        }
      }
    },
    mounted: function () {
      if (window.sessionStorage.getItem('userType') === 'staff') {
        this.$axios.get('/' + window.sessionStorage.getItem('userType') + '/showAllCourses')
          .then((response) => {
            console.log(response);
            if (!response.data) {
              this.$message({
                message: 'Failed to load the data!',
                type: 'warning',
              });
            } else {
              this.tableData = response.data;
              let tempList = [];
              for (let i = 0; i < response.data.length; i++) {
                let type = response.data[i].type;
                if (tempList.indexOf(type) === -1) {
                  tempList.push(type);
                  this.typeList.push({
                    text: type,
                    value: type
                  });
                }
              }
            }
          })
          .catch((error) => {
            console.log(error);
            this.$message.error(error);
          });
      } else if (window.sessionStorage.getItem('userType') === 'college') {
        this.$axios.get('/' + window.sessionStorage.getItem('userType') + '/showCourses', {
          params: {
            collegeId: window.sessionStorage.getItem('userId')
          }
        }).then((response) => {
          console.log(response);
          if (!response.data) {
            this.$message({
              message: 'Failed to load the data!',
              type: 'warning',
            });
          } else {
            this.tableData = response.data;
            let tempList = [];
            for (let i = 0; i < response.data.length; i++) {
              let type = response.data[i].type;
              if (tempList.indexOf(type) === -1) {
                tempList.push(type);
                this.typeList.push({
                  text: type,
                  value: type
                });
              }
            }
          }
        }).catch((error) => {
          console.log(error);
          this.$message.error(error);
        });
      } else if (window.sessionStorage.getItem('userType') === 'student') {
        this.$axios.get('/' + window.sessionStorage.getItem('userType') + '/showCourses', {
          params: {
            email: window.sessionStorage.getItem('userId')
          }
        }).then((response) => {
          console.log(response);
          if (!response.data) {
            this.$message({
              message: 'Failed to load the data!',
              type: 'warning',
            });
          } else {
            this.tableData = response.data;
            let tempList = [];
            for (let i = 0; i < response.data.length; i++) {
              let type = response.data[i].type;
              if (tempList.indexOf(type) === -1) {
                tempList.push(type);
                this.typeList.push({
                  text: type,
                  value: type
                });
              }
            }
          }
        }).catch((error) => {
          console.log(error);
          this.$message.error(error);
        });
      } else {
        this.$router.push({path: '/account/login'});
      }
    },
    methods: {
      filterType(value, row) {
        return row.type === value;
      },
      handleEdit(index, row) {
        this.form.seen = true;
        this.form.addOperation = false;
        this.form.id = row.id;
        this.form.collegeId = row.college;
        this.form.type = row.type;
        this.form.temail = row.temail;
        this.form.size = row.size;
        this.form.period = row.period;
        this.form.price = row.price;
        this.form.scheduleList = row.scheduleList;
      },
      handleAdd(index, row) {
        this.form.seen = true;
        this.form.addOperation = true;
        this.form.collegeId = row.college;
        this.form.type = row.type;
        this.form.temail = row.temail;
        this.form.size = row.size;
        this.form.period = row.period;
        this.form.price = row.price;
        this.form.scheduleList = row.scheduleList;
      },
      removeSchedule(row, index) {
        this.form.scheduleList.splice(index, 1);
      },
      addSchedule() {
        this.form.scheduleList.push({
          courseid: this.form.id,
          starttime: null,
          endtime: null
        });
      },
      submit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.form.addOperation) {
              this.$axios.post('/college/setupCourse', {
                id: 0,
                college: this.form.collegeId,
                type: this.form.type,
                temail: this.form.temail,
                size: this.form.size,
                period: this.form.period,
                price: this.form.price,
                scheduleList: this.form.scheduleList,
                studentList: []
              }).then((response) => {
                console.log(response);
                if (response.data !== 0) {
                  this.tableData.push({
                    id: response.data,
                    college: this.form.collegeId,
                    type: this.form.type,
                    temail: this.form.temail,
                    size: this.form.size,
                    period: this.form.period,
                    price: this.form.price,
                    scheduleList: this.form.scheduleList,
                    studentList: []
                  });
                } else {
                  this.$message({
                    message: 'Failed to load the data!',
                    type: 'warning',
                  });
                }
              }).catch((error) => {
                console.log(error);
                this.$message.error(error);
              });
            } else {
              this.$axios.post('/college/modifyCourse', {
                id: this.form.id,
                college: this.form.collegeId,
                type: this.form.type,
                temail: this.form.temail,
                size: this.form.size,
                period: this.form.period,
                price: this.form.price,
                scheduleList: this.form.scheduleList,
                studentList: []
              }).then((response) => {
                console.log(response);
                if (response.data !== 0) {
                  for (let i = 0; i < this.tableData.length; i++) {
                    if (this.tableData[i].id === this.form.id) {
                      this.tableData[i].college = this.form.collegeId;
                      this.tableData[i].type = this.form.type;
                      this.tableData[i].temail = this.form.temail;
                      this.tableData[i].size = this.form.size;
                      this.tableData[i].period = this.form.period;
                      this.tableData[i].price = this.form.price;
                      this.tableData[i].scheduleList = this.form.scheduleList;
                    }
                  }
                } else {
                  this.$message({
                    message: 'Failed to load the data!',
                    type: 'warning',
                  });
                }
              }).catch((error) => {
                console.log(error);
                this.$message.error(error);
              });
            }
          } else {
            this.$message.error('error submit!');
            return false;
          }
        })
      },
      reset(formName) {
        this.$refs[formName].resetFields();
        this.form.seen = false;
      }
    }
  }
</script>

<style scoped>

</style>



