<template>
  <el-row>
    <el-col :span="6" :offset="9">
      <el-form :model="form" :rules="rules" ref="form" label-width="80px">
        <template v-if="form.type === 'college'">
          <el-form-item label="" prop="collegeId">
            <el-input size="medium" :disabled=true v-model="form.collegeId"></el-input>
          </el-form-item>
          <el-form-item label="" prop="location">
            <el-input size="medium" v-model="form.location"></el-input>
          </el-form-item>
          <el-form-item label="" prop="finance">
            <el-input size="medium" :disabled=true v-model="form.finance"></el-input>
          </el-form-item>
          <el-form-item label="" prop="level">
            <el-input size="medium" :disabled=true v-model="form.level"></el-input>
          </el-form-item>
        </template>
        <template v-else-if="form.type === 'staff'">
          <el-form-item label="" prop="email">
            <el-input size="medium" :disabled=true v-model="form.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input size="medium" v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="title">
            <el-input size="medium" v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="collegeId">
            <el-input size="medium" v-model="form.collegeId"></el-input>
          </el-form-item>
        </template>
        <template v-else>
          <el-form-item label="" prop="email">
            <el-input size="medium" :disabled=true v-model="form.email"></el-input>
          </el-form-item>
          <el-form-item label="" prop="name">
            <el-input size="medium" v-model="form.name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="level">
            <el-input size="medium" :disabled=true v-model="form.level"></el-input>
          </el-form-item>
          <el-form-item label="" prop="deposit">
            <el-input size="medium" :disabled=true v-model="form.deposit"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-col :span="10">
              <el-input size="medium" :disabled=true v-model="form.point"></el-input>
            </el-col>
            <el-col :span="10">
              <el-input size="medium" v-model="form.exchange"></el-input>
            </el-col>
            <el-col :span="2">
              <el-button @click="exchange()"></el-button>
            </el-col>
          </el-form-item>
        </template>
        <el-form-item label="" prop="password">
          <el-input size="medium" type="password" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPassword">
          <el-input size="medium" type="password" v-model="form.checkPassword"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select :disabled=true v-model="form.type" placeholder="">
            <el-option label="" value="student"></el-option>
            <el-option label="" value="staff"></el-option>
            <el-option label="" value="college"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submit('form')"></el-button>
          <el-button @click="logout()"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.form.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        form: {
          collegeId: '',
          location: '',
          finance: '',
          name: '',
          email: '',
          level: '',
          deposit: '',
          point: '',
          exchange: '',
          title: '',
          password: '',
          checkPassword: '',
          type: ''
        },
        rules: {
          collegeId: [
            {required: true, message: '', trigger: 'blur'},
            {min: 7, max: 7, message: ' 7 ', trigger: 'blur,change'}
          ],
          location: [
            {required: true, message: '', trigger: 'blur'}
          ],
          finance: [
            {required: true, message: '', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 3, max: 10, message: ' 3  10 ', trigger: 'blur.change'}
          ],
          email: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'email', message: '', trigger: 'blur,change'}
          ],
          level: [
            {required: true, message: '', trigger: 'blur'}
          ],
          deposit: [
            {required: true, message: '', trigger: 'blur'}
          ],
          title: [
            {required: true, message: '', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
            {min: 5, max: 10, message: ' 5  15 ', trigger: 'blur.change'}
          ],
          checkPassword: [
            {required: true, validator: validatePass, trigger: 'blur'}
          ],
          type: [
            {required: true, message: '', trigger: 'change'}
          ]
        }
      };
    },
    mounted: function () {
      if (!window.sessionStorage.getItem('userType')) {
        this.$router.push({path: '/account/login'});
      } else {
        this.$axios.get('/' + window.sessionStorage.getItem('userType') + '/profile', {
          params: {
            userId: window.sessionStorage.getItem('userId'),
          }
        }).then((response) => {
          console.log(response);
          if (!response.data) {
            this.$message({
              message: 'Failed to load the data!',
              type: 'warning',
            });
            return false;
          }
          if (window.sessionStorage.getItem('userType') === 'college') {
            this.form.collegeId = response.data.id;
            this.form.location = response.data.location;
            this.form.finance = response.data.finance;
            this.form.level = response.data.level;
            this.form.password = response.data.password;
            this.form.checkPassword = response.data.password;
            this.form.type = window.sessionStorage.getItem('userType');
          } else if (window.sessionStorage.getItem('userType') === 'staff') {
            this.form.email = response.data.email;
            this.form.name = response.data.name;
            this.form.title = response.data.title;
            this.form.collegeId = response.data.college;
            this.form.password = response.data.password;
            this.form.checkPassword = response.data.password;
            this.form.type = window.sessionStorage.getItem('userType');
          } else {
            this.form.email = response.data.email;
            this.form.name = response.data.name;
            this.form.level = response.data.level;
            this.form.deposit = response.data.deposit;
            this.form.point = response.data.point;
            this.form.password = response.data.password;
            this.form.checkPassword = response.data.password;
            this.form.type = window.sessionStorage.getItem('userType');
          }
        }).catch((error) => {
          console.log(error);
          this.$router.push({path: '/account/login'});
        });
      }
    },
    methods: {
      submit(formName) {
        this.$refs[formName].validate((valid) => {
          if (!this.form.type) {
            this.$router.push({path: '/account/login'});
          } else {
            this.$axios.get('/' + this.form.type + '/modifyAccount', {
              params: {
                collegeId: this.form.collegeId,
                location: this.form.location,
                password: this.form.password,
                email: this.form.email,
                name: this.form.name,
                title: this.form.title,
              }
            }).then((response) => {
              console.log(response);
              if (response.data !== 0) {
                this.$message({
                  message: 'Modify successfully!',
                  type: 'success',
                });
              } else {
                this.$message({
                  message: 'Please try again',
                  type: 'warning',
                });
              }
            }).catch((error) => {
              console.log(error);
              this.$message.error(error);
            });
          }
        });
      },
      exchange() {
        this.$axios.get('/student/exchange', {
          params: {
            email: this.form.email,
            exchange: this.form.exchange
          }
        })
          .then((response) => {
            console.log(response);
            if (response.data !== 0) {
              this.$message({
                message: 'Exchange successfully!',
                type: 'success',
              });
              this.form.deposit = (parseInt(this.form.deposit) + parseInt(this.form.exchange)).toString();
              this.form.point = (parseInt(this.form.point) - parseInt(this.form.exchange)).toString();
            } else {
              this.$message({
                message: 'Please try again',
                type: 'warning',
              });
            }
          })
          .catch((error) => {
            console.log(error);
            this.$message.error(error);
          });
      },
      logout() {
        this.$axios.get('/' + this.form.type + '/logout')
          .then(function (response) {
            console.log(response);
            window.sessionStorage.removeItem('userId');
            window.sessionStorage.removeItem('userType');
          })
          .catch(function (error) {
            console.log(error);
          });
        this.$router.push({path: '/account/login'});
      }
    }
  }
</script>



